import React, { useState, useEffect } from "react";

const CookieBanner = () => {
  // Set state of cookie banner
  const [showBanner, setShowBanner] = useState(false);

  useEffect(() => {
    if (typeof window === 'undefined') {
      return;
    }
    const isAgree = window.localStorage.getItem('is_agree');
    if (isAgree === '1') {
      setShowBanner(false);
    } else {
      setShowBanner(true);
    }
  }, []);

  // Hide cookie banner when clicked.
  const cookieAgree = () => {
    if (typeof window === 'undefined') {
      return;
    }
    window.localStorage.setItem('is_agree', 1);
    setShowBanner(false);
  };

  return (
    <>
      {
        showBanner ? (
          <div className="cookies" id="cookies__wrapper">
            <div className="default-max-width">
              <p className="cookies__text">
                より良い体験を提供するため、当ウェブサイトではCookieを使用しています。
                引き続き閲覧する場合、Cookieの使用を承諾したものとみなされます。
              </p>
              <button className="button" onClick={cookieAgree}>
                同意する
              </button>
            </div>
          </div>
        ) : null
      }
    </>
  );
};

export default CookieBanner;