// pages/test.js
import React, { useState, useEffect } from 'react';
import { graphql } from 'gatsby';
import '../js/main';

import Layout from '../components/main-layout-ja';
import Seo from '../components/seo';

//Image
import ListBg from '../images/bg/list.svg';
import ListBgSp from '../images/bg/list-sp.svg';
import DefaultArticleImg from '../images/thumb/use_case.png'

const UseCasesPage = ({ data }) => {
  const currentDate = new Date().toISOString();
  const allTags = [
    '製造業',
    '金融',
    'ヘルスケア',
    'モビリティ',
    'インフラ',
    'エネルギー',
    '教育',
    '環境',
    'メディア',
    '行政',
    'エンタメ',
    '化学',
    '建築',
    '防災',
    '航空',
    '観光',
    '地方創生'
  ]
  
  // initial state of filter constant
  const noFilters = [].concat(allTags, undefined);
  
  // State of selected filters
  const [filter, setFilter] = useState(noFilters);

  // State of filter selection/checking
  const [isFilterChecked, setIsFilterChecked] = useState(false);
 
  data = data.allMicrocmsPosts.edges.filter(
    ({ node }) => {
      return (
        node.public_date_ja && node.public_date_ja <= currentDate &&
        (
          filter.includes(node.tags[0]) ||
          filter.includes(node.tags[1]) || 
          filter.includes(node.tags[2]) ||
          filter.includes(node.tags[3]) ||
          filter.includes(node.tags[4]) ||
          filter.includes(node.tags[5]) ||
          filter.includes(node.tags[6]) ||
          filter.includes(node.tags[7]) ||
          filter.includes(node.tags[8]) ||
          filter.includes(node.tags[9]) ||
          filter.includes(node.tags[10]) ||
          filter.includes(node.tags[11]) ||
          filter.includes(node.tags[12]) ||
          filter.includes(node.tags[13]) ||
          filter.includes(node.tags[14]) ||
          filter.includes(node.tags[15]) ||
          filter.includes(node.tags[16])
        )
      )
    }
  );

  data.forEach(function(article){
    const date = new Date(article.node.public_date_ja);
    const formattedDate = date.toLocaleDateString('ja-JP', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
    });
    const desiredFormat = formattedDate.replace(/(\d+)\/(\d+)\/(\d+)/, '$1/$2/$3');
    article.node.public_date_formatted = desiredFormat;
  });

  const allArticles = data;

  // State for the list
  const [list, setList] = useState([...allArticles.slice(0, 15)]);

  // State to trigger load more
  const [loadMore, setLoadMore] = useState(false);

  // State of whether there is more to load
  const [hasMore, setHasMore] = useState(allArticles.length > 15);
  
  // Load more button click
  const handleLoadMore = () => {
    setLoadMore(true);
  };

  // Update the filter array
  const updateFilter = () => {
    const selectedFilters = [...document.querySelectorAll('.filter__checkbox:checked')].map(e => e.value);

    if (selectedFilters.length > 0) {
      setIsFilterChecked(true);
      setFilter(selectedFilters);
    } else {
      setIsFilterChecked(false);
      setFilter(noFilters);
    }
  };

  // Display all articles when selectAll button is clicked
  const showAll = () => {
    setIsFilterChecked(false);
    setFilter(noFilters);
  };

  // Update article list based on selected filters
  useEffect(() => {
    if (isFilterChecked) {
      setList([...allArticles.slice(0, 15)]);
    } else {
      setList([...allArticles.slice(0, 15)]);
    }
  }, [filter, isFilterChecked]); // eslint-disable-line

  //Check if there is more
  useEffect(() => {
    const isMore = list.length < allArticles.length;
    setHasMore(isMore);
  }, [list]); //eslint-disable-line

  // Handle loading more articles
  useEffect(() => {
    if (loadMore && hasMore) {
      const currentLength = list.length;
      const isMore = currentLength < allArticles.length;
      const nextResults = isMore
        ? allArticles.slice(currentLength, currentLength + 15)
        : [];
      setList([...list, ...nextResults]);
      setLoadMore(false);
    }
  }, [loadMore, hasMore]); //eslint-disable-line

  const allTagsHtml = allTags.map((tag) => {
    return <li className="filter__item" key={tag}>
      <input className="filter__checkbox" onChange={updateFilter} value={tag} id={tag} type="checkbox" name="categories"/>
      <label className="tag tag--filter" htmlFor={tag}>{tag}</label>
    </li>
  })  

  return <Layout>
    <React.Fragment>
      <section className="list">
        <picture>
          <source media="(min-width: 768px)" srcSet={ListBg} />
          <img className="list__bg" src={ListBgSp} alt="事例（ユースケース）" />
        </picture>
        <div className="default-max-width">
          <h2 className="heading heading--no-line">
            事例（ユースケース）
          </h2>
          <p className="list__description">
            Trusted Webの考え方を生かした、様々な分野の事例を紹介しています。<br/>（例：ヘルスケア、行政、サプライチェーン、人材スキル等）
          </p>
          <ul className="filter mb-4">
            <li className="filter__item" key="selectAll">
              <input className="filter__radio" onClick={ showAll } id="selectAll" type="radio" name="select-all" defaultChecked/>
              <label className="tag tag--filter" htmlFor="selectAll">すべて</label>
            </li>
            {allTagsHtml}
          </ul>
          <div className="list__articles">
            <ul className="article-list">
              {list.map(edge => {
                const article = edge.node
                const articleTags = article.tags.map(tag => {
                  return <span key={tag} className="tag">{tag}</span>
                })
                const detailLink = article.url
                  ? article.url
                  : "/news/" + article.id;
                const target = article.url
                  ? '_blank'
                  : null;
                let description;

                if (article.body_ja) {
                  const lengthLimit = 50;
                  const regex = /(<([^>]+)>)/ig;
                  const result = article.body_ja.replace(regex, '');

                  description = result.substring(0, lengthLimit)
                  if (result.length > 50) {
                    description = description + '...';
                  }
                }

                return (
                <React.Fragment key={article.id}>
                  <li className="article-item">
                    <article className="article"><a className="article__link" href={detailLink} target={target}>
                      <span className="article__date" suppressHydrationWarning={true}>
                        {article.public_date_formatted}
                      </span>
                      <div className="article__image-wrapper">
                        {
                          article.thumbnail_image
                            ? <img className="article__img" src={article.thumbnail_image.url} alt={article.title_ja} />
                            : <img className="article__img" src={DefaultArticleImg} alt={article.title_ja} />
                        }
                      </div>
                      <h3 className="article__title">{article.title_ja}</h3>
                      {
                        article.body_ja
                          ? 
                          <p className="article__description">{description}</p>
                          : null
                      }
                      
                      <div className="article__tag-wrapper flex-wrap">
                        {articleTags}
                      </div>
                    </a></article>
                  </li>
                </React.Fragment>
                  )
              })}
            </ul>
          </div>
          {
            hasMore
              ? 
              <div className="text-center">
                <button className="button block self-auto" onClick={handleLoadMore}>もっと見る</button>
              </div>
              : null
          }
        </div>
      </section>
    </React.Fragment>
  </Layout>
}

export const query = graphql`
  {
    allMicrocmsPosts(
      sort: { fields: [public_date_ja, createdAt], order: DESC }
      filter: { category: { eq: "ユースケース" } }
    ) {
      edges {
        node {
          id
          title_ja
          body_ja
          tags
          public_date_ja
          createdAt
          category
          thumbnail_image{
            url
            height
            width
           }
        }
      }
    }
  }
`

export default UseCasesPage

export const Head = () => (
  <Seo title="事例（ユースケース）" />
)
